.overlay {
    opacity: 0.9;
}

a {
    color: black;
}

@media only screen and (max-width: 991px) {
    .project-info {
        font-size: 16px;
    }
}

@media only screen and (max-width: 767px) and (min-width: 425px) {
    .project-info {
        font-size: 16px;
    }
}

@media only screen and (max-width: 374px) {
    .project-title {
        font-size: 26px;
    }
    .buffer {
        display: none;
    }
    .project-info {
        font-size: 11px;
        margin: 10px !important;
    }
}